<template>
  <el-main style="background-color: #000000;color:white;font-family: 'Noto Sans SC';font-weight: 300;">
    <el-row type="flex" justify="center" style="padding-top:120px;margin-bottom: 0px;width:100%;">
      <el-col :xs="24" :md="18" :xl="18" style="line-height:40px;border:2px dashed #E91E63;border-radius: 10px;">
        <div type="flex" justify="center"
             style="display:flex;align-items:center;justify-content:center;vertical-align: center;margin-top:10px;">

          <el-col :span="2">
            <img src="rocket.png" style="width:40px;">

          </el-col>
          <el-col :span="22"
                  style="text-align:left;color:#ffffff;font-size:24px;padding-left:20px;margin-top:-10px;">
            {{$t('homeSentence3Part1')}}<span style="color:#E91E63;font-weight: bolder;">${{ poolMoney }}</span>{{$t('homeSentence3Part2')}},
            {{$t('homeSentence4Part1')}}<span style="color:#E91E63;font-weight: bolder;">${{ poolAwardMoney }}</span>{{$t('homeSentence4Part2')}}

          </el-col>
        </div>
      </el-col>
    </el-row>

    <div
        style="padding-top:-80px;padding-bottom:300px;background:url('home-bg-2.png');background-repeat:no-repeat;background-size:100%;background-position:right;width:100%;">
    </div>

    <el-row type="flex" justify="center" style="margin-top:-120px;">
      <el-col :xs="24" :md="18" :xl="18" style="line-height:40px;">
        <!--          <img src="second-home-bg.png" style="width:100%;">-->
        <div class="shadow-border">

          <el-row type="flex" justify="start" :style="'margin-top:60px;flex-direction:row;'">
            <el-col :xs="9" :sm="{span:6,offset:4}">

              <el-row type="flex" justify="start"
                      style="height: 66px;width: 100%;background: linear-gradient(90deg, #E91E63, rgba(26, 26, 26, 0.01));border: 2px solid #E91E63;border-radius: 10px;">

                <div style="width:6%">
                </div>
                <div style="margin-top:10px;">
                  <img v-if="swapCoin=='CFX'" src="cfx-logo.svg" style="width:40px;">
                  <img v-else src="cfx-white.svg" style="width:32px;">
                </div>

                <div style="width:6%">
                </div>
                <div v-if="swapCoin =='CFX'"
                        style="text-align:left;color:#ffffff;font-size:18px;margin-top:12px;">
                  CFX
                </div>
                <div v-else
                        style="text-align:left;color:#ffffff;font-size:18px;margin-top:12px;">
                  PCFX
                </div>
              </el-row>

            </el-col>
            <el-col :xs="{span:4,offset:1}" :sm="{span:2,offset:1}" style="text-align: center;">
              <vs-button icon style="width:100%;margin:0 auto;
                 height: 66px;
                 background: #E91E63;
                 border-radius: 10px;line-height:80px;" @click="exchange">
                <img src="translate.png">
              </vs-button>
            </el-col>
            <el-col :xs="{span:9,offset:1}" :sm="{span:6,offset:1}">
              <el-row type="flex" justify="start"
                  style="height: 66px;width: 100%;background: linear-gradient(90deg, #E91E63, rgba(26, 26, 26, 0.01));border: 2px solid #E91E63;border-radius: 10px;">

                <div style="width:6%">
                </div>
                <div style="margin-top:10px;">
                  <img v-if="swapCoin=='PCFX'" src="cfx-logo.svg" style="width:40px;">
                  <img v-else src="cfx-white.svg" style="width:32px;">
                </div>

                <div style="width:6%">
                </div>
                <div v-if="swapCoin =='PCFX'"
                     style="text-align:left;color:#ffffff;font-size:18px;margin-top:12px;">
                  CFX
                </div>
                <div v-else
                     style="text-align:left;color:#ffffff;font-size:18px;margin-top:12px;">
                  PCFX
                </div>
              </el-row>
            </el-col>
          </el-row>

          <el-row style="margin-top:40px;font-size:18px;">
            <el-col :xs="24" :sm="{span:6,offset:4}">
              <vs-input icon-after type="number" danger state="danger" block
                        v-model="swapMoney" :placeholder="$t('pcfxPlaceholder')" @input="changeSwapMoney">
                <template #icon>
                  <vs-button @click="maxSwapMoney" circle flat
                             warn
                  >
                    {{$t('max')}}
                  </vs-button>
                </template>
              </vs-input>

            </el-col>
            <el-col :xs="{span:0,offset:1}" :sm="{span:2,offset:1}" style="text-align: center;margin-top:-5px;">
              <div style="width: 100%;margin:0 auto;
                 height: 66px;
                 border:2px dashed #E91E63;
                 border-radius: 10px;line-height:80px;">
                <img src="translate.png">
              </div>
            </el-col>
            <el-col :xs="{span:0,offset:1}" :sm="{span:6,offset:1}">

              <vs-input icon-after type="number" danger state="danger" block style="opacity:1"
                        v-model="translateMoney"  disabled :placeholder="$t('pcfxPlaceholder')">
                <template #icon>
                  <vs-button @click="maxSwapMoney" circle flat
                             warn
                  >
                    {{$t('max')}}
                  </vs-button>
                </template>
              </vs-input>
            </el-col>
          </el-row>
          <el-row type="flex" justify="center" style="margin-bottom: 60px;">
            <el-col :xs="12" :sm="8" style="margin-top:30px;line-height:30px;">
              <vs-button @click="swap"
                         block border style="font-size:20px;line-height:40px;"
                         color="#E91E63"
              >
                {{$t('swap')}}
              </vs-button>
            </el-col>
          </el-row>
        </div>
      </el-col>
    </el-row>
    <el-row style="margin-top:120px;line-s">
      <el-col :xs="{span:24}" :md="{span:8,offset:3}" style="line-height:80px;font-size:24px;letter-spacing: 1px;">
        <div class="shadow-border">
          <el-row>
            <el-col :span="6" :offset="2" :style="'text-align: right;color:'+firstPoolColor">
              <div @click="switchPools(0)" style="cursor:pointer">
                {{$t('pools')}}
              </div>
            </el-col>
            <el-col :span="1" :offset="1">
              |
            </el-col>
            <el-col :span="13" :offset="1" :style="'text-align: left;color:'+secondPoolColor">

              <vs-tooltip danger style="width:140px">
                <div @click="switchPools(1)" style="cursor:pointer">
                  {{$t('specialPools')}}
                </div>
                <template #tooltip>
                  {{$t('poolsSentence1')}}
                </template>
              </vs-tooltip>
            </el-col>
          </el-row>
        </div>
      </el-col>
    </el-row>

    <div
        style="margin-top:-120px;padding-bottom:500px;background:url('home-bg-3.png');background-repeat:no-repeat;background-size:100%;background-position:right;width:100%;">
    </div>
    <el-row type="flex" justify="center" class="pool" style="margin-top:-270px;cursor:pointer;">
      <el-col :xs="24" :md="18" :xl="18" style="line-height:40px;">
        <!--          <img src="second-home-bg.png" style="width:100%;">-->
        <div class="shadow-border" @click="gotoPoolPage">
          <el-row>
            <el-col :xs="24" :sm="24" :lg="12">
              <div style="color:#ffffff;">

                <el-row type="flex" justify="center">
                  <el-col :xs="24" :md="20" style="line-height:60px;">
                    <div class="prize-shadow-border" style="margin-top:40px;">
                      <el-row type="flex" justify="center">
                        <div style="margin-top:10px;text-align:right;">
                          <img class="img-width" style="text-align:right;"
                               src="cfx-logo.svg"/>
                        </div>
                        <div
                            style="text-align:left;color:#ffffff;font-size:18px;margin-top:12px;">

                          <div class="money-text"
                               style="margin-left:20px;text-align:left;font-weight: 800;display: inline-block;color: #FFFFFF;background: linear-gradient(77.7398deg, #C9225B 0.9521484375%, #FF3278 60.6689453125%, #FC9E7C 100%);-webkit-background-clip: text;-webkit-text-fill-color: transparent; letter-spacing:8px">
                            ${{ poolAwardMoney }}
                          </div>
                        </div>
                      </el-row>
                      <el-row>
                        <div style="margin-top:-10px;padding-bottom: 10px;">
                          {{$t('poolsSentence2')}}
                        </div>
                      </el-row>

                    </div>
                  </el-col>
                </el-row>

              </div>
            </el-col>
            <el-col :xs="24" :sm="24" :lg="12">
              <div style="margin-top:30px;">
                <el-row type="flex" justify="center" style="justify-content: center;align-items: center;">
                  <el-col :span="24">
                    <div style="width:100%;">
                      <circular-count-down-timer
                          :initial-value="0"
                          :stroke-width="3"
                          :seconds-stroke-color="'#ffffff'"
                          :minutes-stroke-color="'#ffffff'"
                          :hours-stroke-color="'#ffffff'"
                          :days-stroke-color="'#ffffff'"
                          :underneath-stroke-color="'#E91E63'"
                          :seconds-fill-color="'rbga(#ffffff,0)'"
                          :minutes-fill-color="'rbga(#ffffff,0)'"
                          :hours-fill-color="'rbga(#ffffff,0)'"
                          :days-fill-color="'rbga(#ffffff,0)'"
                          :size="countdownTimerWidth"
                          :padding="countdownTimerPadding"
                          :day-label="$t('days')"
                          :hour-label="$t('hours')"
                          :minute-label="$t('minutes')"
                          :second-label="$t('seconds')"
                          :show-second="true"
                          :show-minute="true"
                          :show-hour="true"
                          :show-negatives="false"
                          :paused="false"
                          :notify-every="'minute'"
                          style="color:#ffffff;"
                          ref="timer"
                      ></circular-count-down-timer>
                    </div>
                  </el-col>
                </el-row>
                <el-row style="margin-top: 30px;">
                  <el-col :offset="1" :span="10">
                    <vs-button block @click.stop="showDepositDialog"
                               style="font-size:20px;line-height:40px;background:linear-gradient(90deg, #E91E63, #FC9E7C);"
                               color="#E91E63">
                      {{$t('deposit')}}
                    </vs-button>
                  </el-col>
                  <el-col :span="10" :offset="2">
                    <vs-button @click.stop="showWithdrawDialog" block border style="font-size:20px;line-height:40px;"
                               color="#E91E63">
                      {{$t('withdraw')}}
                    </vs-button>
                  </el-col>
                </el-row>
                <el-row style="color:white;margin-bottom: 30px;margin-top:10px;text-align:left;">

                  <el-col :offset="1" :span="1" style="">
                    <div style="display:inline-block;width: 20px;height: 20px;background: linear-gradient(78deg, #F1508E 0%, #F56988 35%, #F88382 81%, #FC9E7C 100%);
  border-radius: 50%;">
                    </div>
                  </el-col>

                  <el-col :span="10" style="margin-top:-4px;padding-left:10px;">
                    *%{{$t('apy')}}
                  </el-col>

                  <el-col :xs="{span:8,offset:3}" :sm="{span:4,offset:7}" style="text-align: right;margin-top:-4px">
                    {{$t('viewPools')}}
                  </el-col>
                </el-row>
              </div>
            </el-col>

          </el-row>
        </div>
      </el-col>
    </el-row>
    <el-row style="margin:0 auto;margin-top:40px;margin-bottom:100px;color:#999999">
      {{$t('moneyMore')}}
    </el-row>


    <vs-dialog :loading="depositDialogLoading" width="300px" not-center prevent-close v-model="depositDialogShow">
      <template #header>
        <h4 class="not-margin">
          {{$t('poolsSentence3part1')}} <b>{{$t('poolsSentence3part2')}} </b> {{$t('poolsSentence3part3')}}
        </h4>
      </template>


      <div class="con-content">
        <el-row  v-if="isNeedApprove" style="font-size: 12px;">{{ $t('noticeApproveContent') }}</el-row>

        <vs-input icon-after v-else v-model="depositPoolMoney"  :placeholder="$t('noticeOwnPcfx')+pcfxNumber.toFormat(2)" type="number">
          <template #icon>
            <vs-button @click="maxDepositPoolMoney()" circle flat
                       warn
                        >
              {{$t('max')}}
            </vs-button>
          </template>
        </vs-input>
      </div>

      <template #footer>
        <div class="con-footer">
          <vs-button v-if="isNeedApprove" @click="confirmApprove" transparent>
            {{$t('noticeApprove')}}
          </vs-button>
          <vs-button v-else @click="confirmDeposit" transparent>
            {{$t('noticeDeposit')}}
          </vs-button>
          <vs-button @click="depositDialogShow=false" dark transparent>
            {{$t('noticeCancel')}}
          </vs-button>
        </div>
      </template>
    </vs-dialog>


    <vs-dialog :loading="withdrawPoolDialogLoading" width="300px" not-center prevent-close v-model="withdrawPoolDialogShow">
      <template #header>
        <h4 class="not-margin">
          {{$t('noticeWithdraw')}} <b>PCFX</b>
        </h4>
      </template>


      <div class="con-content">
        <vs-input icon-after v-model="withdrawPoolMoney" :placeholder="$t('noticeWithdrawPcfx')+ticketNumber.toFormat(2)" type="number">
          <template #icon>
            <vs-button @click="maxWithdrawPoolMoney" circle flat
                       warn
            >
              {{$t('max')}}
            </vs-button>
          </template>
        </vs-input>
        <!--        <p style="font-size:10px;text-align:left;">倒计时提现：等待一定时间后，从账户页面点击提现；</p>-->
        <!--        <p style="font-size:10px;text-align:left;">立即提现：若客户信用积分不够，需要一定的少量提现费用，该部分费用用于确保防止用户恶意参与，所有的提现费用会进入奖池，作为用户奖金。</p>-->

      </div>

      <template #footer>
        <div class="con-footer">
          <vs-button @click="showTimelockDialog" transparent>
            {{$t('noticeCountdownWithdraw')}}
          </vs-button>
          <vs-button @click="showImmediateDialog" transparent>
            {{$t('noticeNowWithdraw')}}
          </vs-button>
          <vs-button @click="withdrawPoolDialogShow=false" dark transparent>
            {{$t('noticeCancel')}}
          </vs-button>
        </div>
      </template>
    </vs-dialog>
    <vs-dialog :loading="timelockDialogLoading" width="300px" not-center prevent-close v-model="timelockDialogShow">
      <template #header>
        <h4 class="not-margin">
          {{$t('noticeSureCountdownWithdrawPart1')}}<b> {{$t('noticeSureCountdownWithdrawPart2')}}</b>
        </h4>
      </template>


      <div class="con-content">
        <p>
          {{$t('noticeCountdownWithdrawInfo')}}
        </p>
      </div>

      <template #footer>
        <div class="con-footer">
          <vs-button @click="withdrawWithTimelockFrom" transparent>
            {{$t('noticeConfirm')}}
          </vs-button>
          <vs-button @click="timelockDialogShow=false" dark transparent>
            {{$t('noticeCancel')}}
          </vs-button>
        </div>
      </template>
    </vs-dialog>

    <vs-dialog :loading="immediateDialogLoading" width="550px" not-center prevent-close v-model="immediateDialogShow">
      <template #header>
        <h4 class="not-margin">
          {{$t('noticeSureNowWithdrawPart1')}}<b>{{$t('noticeSureNowWithdrawPart2')}}</b>
        </h4>
      </template>


      <div class="con-content">
        <p style="text-align:left">
          {{$t('noticeNowWithdrawInfo')}}
        </p>
        <vs-input v-model="maximumExitFee" :placeholder="$t('noticeMaxWithdrawFee')+(withdrawPoolMoney*0.01).toFixed(2)" type="number"></vs-input>

      </div>

      <template #footer>
        <div class="con-footer">
          <vs-button @click="withdrawInstantlyFrom" transparent>
            {{$t('noticeConfirm')}}
          </vs-button>
          <vs-button @click="closeImmediateDialog" dark transparent>
            {{$t('noticeCancel')}}
          </vs-button>
        </div>
      </template>
    </vs-dialog>
  </el-main>
</template>


<script>
import config from "@/config";
import utils from "@/utils/utils";
import {BigNumber} from "bignumber.js";
import {Conflux} from "js-conflux-sdk";
import {mapActions} from "vuex";


export default {
  name: "pools",
  components: {
  },
  data() {
    return {
      poolBalance: 0,
      poolAwardBalance: 0,
      poolAwardMoney:0,
      poolMoney:0,

      prizePeriodRemainingSeconds: new BigNumber(0),
      depositDialogShow: false,
      swapMoney: '',
      translateMoney: '',
      windowHeight: 0,
      windowWidth: 0,
      choosePool: 0,
      swapCoin: 'CFX',
      depositPoolDialogShow: false,
      depositPoolMoney: '',
      withdrawPoolDialogShow: false,
      withdrawPoolMoney: '',
      immediateDialogShow: false,
      timelockDialogShow: false,
      withdrawPoolDialogLoading:false,
      depositDialogLoading: false,
      timelockDialogLoading:false,
      immediateDialogLoading:false,
      pcfxNumber: new BigNumber(0),
      ticketNumber:new BigNumber(0),
      pcfxApy:'0%',
      maximumExitFee:'',
      intervalTimer:null,
      isNeedApprove:false,
      isFirstRun:true,
      dripRatePerSecond:0,

    };
  },
  computed: {
    pcfxContract: function () {
      return this.$store.state.web3.pcfxContract;
    },
    ticketContract: function () {
      return this.$store.state.web3.ticketContract;
    },
    poolGoContract: function () {
      return this.$store.state.web3.poolGoContract;
    },
    prizePoolContract: function () {
      return this.$store.state.web3.prizePoolContract;
    },
    periodicPrizeStrategyContract: function () {
      return this.$store.state.web3.periodicPrizeStrategyContract;
    },
    tokenFaucetContract: function () {
      return this.$store.state.web3.tokenFaucetContract;
    },
    cfxStakeContract: function () {
      return this.$store.state.web3.cfxStakeContract;
    },
    firstPoolColor: function () {
      if (this.choosePool == 0) {
        return '#E91E63'
      } else
        return '#FFFFFF'
    },
    secondPoolColor: function () {
      if (this.choosePool == 1) {
        return '#E91E63'
      } else
        return '#FFFFFF'
    },
    countdownTimerWidth: function () {
      console.log(this.windowWidth);
      if (this.windowWidth < 500) {
        return this.windowWidth / 5
      } else if (this.windowWidth <= 1200) {
        return this.windowWidth / 5
      } else {
        return this.windowWidth / 12
      }
    },
    countdownTimerPadding: function () {
      if (this.windowWidth < 500) {
        return 3
      } else if (this.windowWidth <= 768) {
        return 8
      } else {
        return 10
      }
    },
  },
  created() {
    const that = this;
    that.windowHeight = parseInt(`${window.innerHeight}`);
    that.windowWidth = parseInt(`${window.innerWidth}`);
    window.onresize = () => {
      return (() => {
        that.windowHeight = parseInt(`${window.innerHeight}`);
        that.windowWidth = parseInt(`${window.innerWidth}`);
      })();
    }
  },


  async mounted() {
    var that = this;
    that.prizePeriodRemainingSeconds = (await that.periodicPrizeStrategyContract.prizePeriodRemainingSeconds())
    that.$nextTick(function(){
      console.log(that.prizePeriodRemainingSeconds.toNumber());
      that.$refs.timer.updateTime(that.prizePeriodRemainingSeconds.toNumber());
    })

    let dripRatePerSecond = await that.tokenFaucetContract.dripRatePerSecond();
    that.dripRatePerSecond = dripRatePerSecond;
    that.update();
    that.intervalTimer = setInterval(that.update, 6000)
  },
  beforeDestroy() {
    var that = this;
    clearTimeout(that.intervalTimer);
  },
  methods: {

    ...mapActions(['cfxLogin']),

    changeSwapMoney() {
      this.translateMoney = this.swapMoney;
    },
    async swap() {
      var that = this;
      if (that.$store.state.web3.account == null) {
        await that.cfxLogin();
      }

      if (that.$store.state.web3.isPortalInstalled == false) {
        that.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: that.$t('noticeWallet'),
        })
        return;
      }
      if (that.$store.state.web3.account == null) {
        that.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: that.$t('noticeAddr'),
        })
        return;
      }
      if (that.swapMoney == '' || that.swapMoney <=0){
        this.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: that.$t('correctNumPlaceholder'),
        })
        return;
      }
      if (that.swapCoin == "CFX") {
        let status = await that.pcfxContract.deposit(that.$store.state.web3.account, that.swapMoney);
        console.log(status);
        if (status.succ == true) {
          this.$vs.notification({
            // width:'100%',
            duration: 6000,
            icon: "<i class='bx bx-select-multiple' ></i>",
            progress: 'auto',
            color: 'success',
            position: 'top-center',
            title: that.$t('info'),
            text: that.$t('noticeCfxTranslate'),
          })
        } else {
          this.$vs.notification({
            // width:'100%',
            duration: 10000,
            icon: "<i class='bx bx-error' ></i>",
            progress: 'auto',
            color: 'danger',
            position: 'top-center',
            title: that.$t('error'),
            text: status.message,
          })
        }
      } else {
        let status = await that.pcfxContract.withdraw(that.$store.state.web3.account, that.swapMoney);

        if (status.succ == true) {
          this.$vs.notification({
            // width:'100%',
            duration: 6000,
            icon: "<i class='bx bx-select-multiple' ></i>",
            progress: 'auto',
            color: 'success',
            position: 'top-center',
            title: that.$t('info'),
            text: that.$t('noticePcfxTranslate'),
          })
        } else {
          this.$vs.notification({
            // width:'100%',
            duration: 10000,
            icon: "<i class='bx bx-error' ></i>",
            progress: 'auto',
            color: 'danger',
            position: 'top-center',
            title: that.$t('error'),
            text: status.message,
          })
        }
      }

      that.updateCfxAndPcfxBalance();

    },

    exchange() {
      if (this.swapCoin == 'CFX') {
        this.swapCoin = 'PCFX'
        this.obtainCoin = 'CFX'
      } else {
        this.swapCoin = 'CFX'
        this.obtainCoin = 'PCFX'
      }
    },
    switchPools(order) {
      var that =this;
      if(order == 1){

        that.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'warn',
          position: 'top-center',
          title:  that.$t('noticePrompt'),
          text: that.$t('noticeSentece1'),
        })
        return;
      }
      this.choosePool = order;
    },
    gotoPoolPage() {
      this.$router.push({
        path: '/pool'
      })
    },
    async update() {
      var that = this;

      // console.log("cToken "+await that.prizePoolContract.cToken());
      var totalFunds = await that.cfxStakeContract.balanceOfUnderlyingView();

      var poolBalance = await that.prizePoolContract.accountedBalance();

      that.prizePeriodRemainingSeconds = (await that.periodicPrizeStrategyContract.prizePeriodRemainingSeconds())

      let interest = totalFunds.multipliedBy(that.prizePeriodRemainingSeconds.multipliedBy(0.0408).div(31536000));
      // console.log("tickets" + tickets)
      var award = totalFunds.plus(interest).minus(poolBalance);

      let poolAwardMoney = award.multipliedBy(that.$store.state.web3.pcfxPrice);
      let poolMoney = poolBalance.multipliedBy(that.$store.state.web3.pcfxPrice);
      that.poolAwardMoney = poolAwardMoney.toFormat(0);
      that.poolMoney = poolMoney.toFormat(0);
      that.poolAwardBalance = award.toFormat(0);
      that.pcfxApy = (new BigNumber(that.dripRatePerSecond * 60 * 60 * 24 * 365))
          .multipliedBy(that.$store.state.web3.poolgoPrice).div(poolBalance / that.$store.state.web3.pcfxPrice).multipliedBy(100).toFormat(1);

    },

    async showWithdrawDialog() {
      var that = this;

      if (that.$store.state.web3.account == null) {
        await that.cfxLogin();
      }


      if (that.$store.state.web3.isPortalInstalled == false) {
        that.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: that.$t('noticeWallet'),
        })
        return;
      }
      if (that.$store.state.web3.account == null) {
        that.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: that.$t('noticeAddr'),
        })
        return;
      }

      that.withdrawPoolDialogShow = true;
      that.withdrawPoolDialogLoading = true;

      that.ticketNumber =  (await that.ticketContract.balanceOf(that.$store.state.web3.account));
      that.withdrawPoolDialogLoading = false;
      that.withdrawPoolMoney = '';

    },
    showTimelockDialog() {

      var that = this;
      if (that.withdrawPoolMoney == '' || that.withdrawPoolMoney <=0){
        that.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: that.$t('correctNumPlaceholder'),
        })
        return;
      }
      this.withdrawPoolDialogShow = false;
      this.timelockDialogShow = true;
    },
    showImmediateDialog() {

      var that = this;
      if (that.withdrawPoolMoney == '' || that.withdrawPoolMoney <=0){
        that.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: that.$t('correctNumPlaceholder'),
        })
        return;
      }
      this.withdrawPoolDialogShow = false;
      this.immediateDialogShow = true;
      this.maximumExitFee = '';
    },
    closeImmediateDialog(){
      this.withdrawPoolDialogShow = true;
      this.immediateDialogShow = false;
    },
    closeTimeoutDialog(){
      this.withdrawPoolDialogShow = true;
      this.tiemoutDialogShow = false;
    },
    async withdrawWithTimelockFrom() {
      let that = this;
      that.timelockDialogLoading =true;
      let status = await that.prizePoolContract.withdrawWithTimelockFrom(that.$store.state.web3.account, that.$store.state.web3.account, that.withdrawPoolMoney, config.ticketAddress);
      that.timelockDialogLoading =false;
      if (status.succ == true) {
        console.log(status);
        let endBlockTime = new BigNumber(status.decodeLogs[status.decodeLogs.length - 1].object.unlockTimestamp).toNumber();

        this.confluxReader = new Conflux({
          url: config.web3Provider,
          networkId: config.networkId
        });
        let blockStatus = await this.confluxReader.getBlockByHash(status.blockHash);
        let seconds = endBlockTime - blockStatus.timestamp;
        console.log(seconds)
        let formatTime = utils.formatSeconds(seconds);
        this.$vs.notification({
          // width:'100%',
          duration: 6000,
          icon: "<i class='bx bx-select-multiple' ></i>",
          progress: 'auto',
          color: 'success',
          position: 'top-center',
          title: that.$t('info'),
          text:  that.$t('noticeSentece2Part1') + formatTime + that.$t('noticeSentece2Part2'),
        })
      } else {
        this.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: status.message,
        })
        return;
      }

      that.withdrawPoolDialogShow = false;
      that.timelockDialogShow = false;

      that.updateCfxAndPcfxBalance();
    }
    ,
    async withdrawInstantlyFrom() {
      let that = this;

      if (that.maximumExitFee == '' || that.maximumExitFee <0){
        that.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: that.$t('correctNumPlaceholder'),
        })
        return;
      }
      that.immediateDialogLoading =true;
      let status = await that.prizePoolContract.withdrawInstantlyFrom(that.$store.state.web3.account, that.withdrawPoolMoney, config.ticketAddress, that.maximumExitFee);
      that.immediateDialogLoading =false;
      console.log(status)
      if (status.succ == true) {

        this.$vs.notification({
          // width:'100%',
          duration: 6000,
          icon: "<i class='bx bx-select-multiple' ></i>",
          progress: 'auto',
          color: 'success',
          position: 'top-center',
          title: that.$t('info'),
          text: that.$t('noticeSentece3'),
        })
        // this.showStakeDialog();
      } else {
        this.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: status.message,
        })
        return;
      }

      that.withdrawPoolDialogShow = false;
      that.immediateDialogShow = false;
      that.updateCfxAndPcfxBalance();
    },
    async showDepositDialog() {
      var that = this;

      if (that.$store.state.web3.account == null) {
        await that.cfxLogin();
      }


      if (that.$store.state.web3.isPortalInstalled == false) {
        that.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: that.$t('noticeWallet'),
        })
        return;
      }

      if (that.$store.state.web3.account == null) {
        that.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: that.$t('noticeAddr'),
        })
        return;
      }
      that.depositDialogShow = true;
      that.depositDialogLoading = true;

      let curAllowance = await that.pcfxContract.allowance(this.$store.state.web3.account, config.prizePoolAddress);
      if (curAllowance.isLessThan(1000000000000)) {
        that.isNeedApprove = true;
      }
      that.pcfxNumber = (await that.pcfxContract.balanceOf(that.$store.state.web3.account));
      that.depositDialogLoading = false;
      that.depositPoolMoney ='';
    },
    async confirmApprove(){
      let that = this;

      that.depositDialogLoading = true;
      let curAllowance = await that.pcfxContract.allowance(this.$store.state.web3.account, config.prizePoolAddress);
      if (curAllowance.isLessThan(1000000000000)) {
        let status = await that.pcfxContract.approveMax(this.$store.state.web3.account, config.prizePoolAddress);

        that.depositDialogLoading = false;
        console.log(status)

        if (status.succ == true) {
          that.isNeedApprove = false;
        } else {

          this.$vs.notification({
            // width:'100%',
            duration: 10000,
            icon: "<i class='bx bx-error' ></i>",
            progress: 'auto',
            color: 'danger',
            position: 'top-center',
            title: that.$t('error'),
            text: status.message,
          })
          that.isNeedApprove = true;
          return;
        }
      }
      that.depositDialogLoading = false;

    }
    ,
    async confirmDeposit() {
      let that = this;

      if (that.depositPoolMoney == '' || that.depositPoolMoney <=0){
        that.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: that.$t('correctNumPlaceholder'),
        })
        return;
      }
      that.depositDialogLoading = true;


      let depositStatus = await that.prizePoolContract.depositTo(this.$store.state.web3.account, this.$store.state.web3.account, that.depositPoolMoney, config.ticketAddress, 'CFXTEST:TYPE.NULL:AAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAA6F0VRCSW');

      that.depositDialogLoading = false;
      console.log(depositStatus)
      if (depositStatus.succ == true) {
        that.depositDialogShow = false;

        this.$vs.notification({
          // width:'100%',
          duration: 6000,
          icon: "<i class='bx bx-select-multiple' ></i>",
          progress: 'auto',
          color: 'success',
          position: 'top-center',
          title: that.$t('info'),
          text: that.$t('noticeSentece4'),
        })
      } else {
        this.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: depositStatus.message,
        })
        return;
      }
      that.updateCfxAndPcfxBalance();

    }
    ,
    async updateCfxAndPcfxBalance(){
      let that = this;
      let pcfxBalance = await that.pcfxContract.balanceOf(that.$store.state.web3.account);
      that.$store.commit('UPDATE_PCFX_BALANCE', pcfxBalance);
      let balance = await this.$store.state.web3.confluxReader.getBalance(that.$store.state.web3.account)
      that.$store.commit('UPDATE_BALANCE', BigNumber(balance).div(BigNumber(10 ** 18)))

    },
    maxWithdrawPoolMoney(){
      let that = this;
      that.withdrawPoolMoney = that.ticketNumber.toFixed(2);
    },
    maxDepositPoolMoney(){
      let that = this;
      that.depositPoolMoney = that.pcfxNumber.toFixed(2);
    },
    maxSwapMoney(){
      let that = this;

      if (that.swapCoin == 'CFX') {
        that.swapMoney = that.$store.state.web3.balance.toFixed(2);
      }else{
        that.swapMoney = that.$store.state.web3.pcfxBalance.toFixed(2);
      }
      that.translateMoney = that.swapMoney;
    },
  },
}
;
</script>


